.card {
  cursor: pointer;
  max-width: 28.33%;
  height: 340px;
  margin: 25px auto;
  border-radius: 10px;
  display: flex;
  align-items: flex-end;
  flex: 1 0 27%;
  text-decoration: none;
  transition: transform 0.3s ease;
  &:hover {
    transform: scale(1.03);
  }
  &--title {
    margin: 20px;
    font-size: 18px;
    font-weight: 500;
    color: white;
  }
}

@media (max-width: 992px) {
  .card {
    max-width: 100%;
    height: 255px;
    flex: 1 0 100%;
    margin: 10px auto;
  }
}
