.about--pic {
  height: 223px;
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), center/cover no-repeat url("../../assets/about.png");
  max-width: 1240px;
  width: 87%;
  margin: 40px auto 0 auto;
  border-radius: 25px;
}

.dropdowns {
  display: flex;
  flex-direction: column;
  margin: 31px auto 0 auto;
  width: 87%;
}

.dropdowns .dropdown {
  width: 87%;
  margin: 0 auto 31px auto;
}

.dropdowns .dropdown--content {
  padding: 20px 10px 10px 20px;
}

@media (max-width: 992px) {
  .about--pic {
    width: 89%;
    margin: 20px auto 0 auto;
    border-radius: 25px;
  }
  .dropdowns {
    margin: 20px auto 0 auto;
  }
  .dropdowns .dropdown {
    width: 100%;
    margin: 0 auto 20px auto;
  }
  .dropdowns .dropdown--content {
    padding: 10px 10px 0 20px;
  }
}
