.footer {
  width: 100%;
  height: 209px;
  background-color: black;
  color: white;
  display: flex;
  align-items: center;
  flex-direction: column;
  &--logo {
    margin: 66px 0 20px 0;
    width: 122px;
  }
  &--copyright {
    font-size: 24px;
    font-weight: 500;
  }
}

@media (max-width: 992px) {
  .footer {
    &--copyright {
      font-size: 12px;
    }
  }
}
